.App {
  background-color: #f9f7f8;
  color: #fff;
  height: 100vh;
  width: 100vw;
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
}

/* global css classes to use wherever needed */
.center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.heading {
  font-size: 92px;
  margin: 0;
  padding: 0;
}
